@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(/static/media/Inter-Regular.dc131113.woff2) format("woff2"),
    url(/static/media/Inter-Regular.3ac83020.woff) format("woff");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url(/static/media/Inter-Medium.75db5319.woff2) format("woff2"),
    url(/static/media/Inter-Medium.c0638bea.woff) format("woff");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url(/static/media/Inter-Black.661569af.woff2) format("woff2"),
    url(/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

.page {
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000000;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  color: #ffffff;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 0 41px 0;
  width: 880px;
  min-width: 320px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 900px) {
  .header {
    width: 100%;
    padding: 0 0 32px 0;
  }
}

.header__logo {
  width: 142px;
  margin: 45px 0 0;
}

@media screen and (max-width: 900px) {
  .header__logo {
    width: 104px;
    margin: 28px 0 0 27px;
  }
}
.content {
  display: flex;
  flex-direction: column;
}
.profile {
  display: grid;
  grid-template-columns: 120px 366px auto;
  margin: 0 auto;
  width: 880px;
  box-sizing: border-box;
  padding: 40px 0 0;
}

@media screen and (max-width: 920px) {
  .profile {
    margin: 0;
    grid-template-columns: auto auto auto;
    width: 100%;
    padding: 40px 27px 0 27px;
  }
}

@media screen and (max-width: 720px) {
  .profile {
    grid-template-columns: 1fr;
    justify-items: center;
    min-width: 320px;
    padding: 42px 0 0;
  }
}

.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

.profile__avatar:hover {
  opacity: 0.2;
  cursor: pointer;
}

@media screen and (max-width: 720px) {
  .profile__avatar {
    margin: 0 auto;
  }
}

.profile__avatar-container {
  background-image: url(/static/media/edit-pen.d476a8b8.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

.profile__name-with-button {
  display: flex;
  justify-content: space-between;
  width: 336px;
}

@media screen and (max-width: 720px) {
  .profile__name-with-button {
    justify-content: center;
    max-width: 282px;
    width: 100%;
    margin: 9px 0 0 3px;
  }
}

.profile__info {
  margin: 0 0 0 30px;
  width: 366px;
  align-self: center;
}

@media screen and (max-width: 720px) {
  .profile__info {
    display: flex;
    margin: 0;
    flex-direction: column;
    max-width: 282px;
    width: 100%;
  }
}

.profile__name {
  margin: 0;
  max-width: 294px;
  font-size: 42px;
  font-weight: 500;
  line-height: calc(48 / 42);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 720px) {
  .profile__name {
    max-width: 189px;
    margin: 22px 10px 0 20px;
    font-size: 27px;
    line-height: calc(27 / 32.5);
  }
}

.profile__edit-button {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid white;
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  margin: 5px 0 0;
  align-self: center;
  justify-self: end;
  background-image: url(/static/media/edit-icon.6bfdb354.svg);
  background-repeat: no-repeat;
  background-position: center;
  transition: opacity 0.3s ease;
}

.profile__edit-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 720px) {
  .profile__edit-button {
    margin: 23px 0 0;
    background-position: center;
    background-size: 40%;
    width: 18px;
    height: 18px;
    padding: 4px;
  }
}

.profile__description {
  margin: 8px 0 0;
  font-size: 18px;
  font-weight: 400;
  line-height: calc(22 / 18);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 720px) {
  .profile__description {
    margin: 14px auto 0;
    font-size: 14px;
    line-height: calc(14 / 17);
    max-width: 282px;
    width: 100%;
    text-align: center;
  }
}

.profile__add-button {
  background-color: rgba(0, 0, 0, 0);
  background-image: url(/static/media/add-icon.b2fce4ac.svg);
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid #fff;
  color: #fff;
  width: 150px;
  height: 50px;
  box-sizing: border-box;
  margin: 0;
  justify-self: end;
  align-self: center;
  transition: opacity 0.3s ease;
}

.profile__add-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 720px) {
  .profile__add-button {
    margin: 36px auto 0;
    justify-self: center;
    align-self: start;
    width: 90%;
    max-width: 282px;
    background-size: 5%;
  }
}

.galary {
  width: 880px;
  margin: 3px auto 0;
}

@media screen and (max-width: 900px) {
  .galary {
    width: 100%;
  }
}

.galary__cards-list {
  margin: 0;
  padding: 47px 0 0;
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px 17px;
  gap: 20px 17px;
  font-size: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 900px) {
  .galary__cards-list {
    grid-template-columns: repeat(2, 1fr);
    padding: 47px 10px 0;
  }
}

@media screen and (max-width: 580px) {
  .galary__cards-list {
    padding: 36px 10px 0;
    grid-template-columns: 1fr;
    min-width: 320px;
  }
}

.card {
  width: 282px;
  border-radius: 10px;
  overflow: hidden;
  margin: 0;
  position: relative;
}

@media screen and (max-width: 900px) {
  .card {
    margin: 0 auto;
    width: 94%;
  }
}

.card__delete {
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0);
  background-image: url(/static/media/trash-icon.4831282e.svg);
  border: none;
  min-width: 18px;
  height: 19px;
  position: absolute;
  top: 20px;
  right: 20px;
  transition: opacity 0.3s ease;
}

.card__delete:hover {
  opacity: 0.6;
  cursor: pointer;
}

.card__delete_visible {
  visibility: visible;
}

.card__img-container {
  width: 100%;
  height: 282px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__img:hover {
  cursor: pointer;
}

.card__description {
  margin: 0;
  padding: 25px 20px 22px;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  color: #000;
  width: 100%;
  align-items: center;
  box-sizing: border-box;
}

.card__name {
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 24px;
  font-weight: 900;
  line-height: calc(29 / 24);
}

.card__like-block {
  display: flex;
  flex-direction: column;
  min-width: 22px;
  min-height: 35px;
  margin: 0 0 0 30px;
  padding: 0;
}

.card__like-counter {
  margin: 3px 0 0;
  color: #000;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;
}

.card__like {
  background-color: #fff;
  background-image: url(/static/media/like-icon.eec224cb.svg);
  border: none;
  min-width: 22px;
  height: 19px;

  justify-self: flex-end;
  transition: opacity 0.3s ease;
}

.card__like:hover {
  opacity: 0.5;
  cursor: pointer;
}

.card__like_active {
  background-image: url(/static/media/like-icon-active.302bdf76.svg);
}

.card__like_active:hover {
  opacity: 0.8;
}

.footer {
  margin: 0 auto;
  padding: 47px 0 60px;
  width: 880px;
}

@media screen and (max-width: 920px) {
  .footer {
    width: 100%;
  }
}

@media screen and (max-width: 563px) {
  .footer {
    box-sizing: border-box;
    padding: 48px 19px 36px;
    min-width: 282px;
  }
}

.footer__text {
  margin: 0;
  color: #545454;
  font-size: 18px;
  font-weight: 400;
  line-height: calc(18 / 21.78);
}

@media screen and (max-width: 563px) {
  .footer__text {
    font-size: 14px;
    line-height: calc(14 / 17);
  }
}

.pop-up {
  visibility: hidden;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: visibility 1s, opacity 0.3s linear;
}

.pop-up__form {
  background-color: #ffffff;
  color: #000000;
  width: 430px;
  margin: auto;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  position: relative;
}

@media screen and (max-width: 583px) {
  .pop-up__form {
    width: 282px;
    padding: 25px 22px;
  }
}

.pop-up_opened {
  visibility: visible;
  opacity: 1;
}

.pop-up_image {
  background-color: rgba(0, 0, 0, 0.9);
}

.pop-up__figure {
  max-width: 70vw;
  max-height: 85vh;
  margin: auto;
  box-sizing: border-box;
  position: relative;
}

.pop-up__image {
  width: 100%;
  object-fit: cover;
}

.pop-up__label {
  font-size: 12px;
  font-weight: 400;
  line-height: calc(15 / 12);
}

.pop-up__close-button {
  position: absolute;
  width: 32px;
  height: 32px;
  top: -40px;
  right: -40px;
  background-image: url(/static/media/close-Icon.e2e95a17.svg);
  background-color: rgba(0, 0, 0, 0);
  background-size: cover;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;
}

.pop-up__close-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 583px) {
  .pop-up__close-button {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}

.form {
}

.form__header {
  font-size: 24px;
  font-weight: 900;
  line-height: calc(24 / 29);
  margin: 0 0 49px;
}

@media screen and (max-width: 583px) {
  .form__header {
    font-size: 18px;
    line-height: calc(18 / 21.7);
    margin: 0 0 45px;
  }
}

.form__header_delete {
  margin: 0 0 21px;
}

.form__fields {
  margin: 0;
  border: none;
  width: 100%;
  padding: 0;
}

.form__item {
  border: 0;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  padding: 8px 0 0;
}

.form__item:focus {
  outline: 1px solid rgba(0, 0, 0, 0.2);
}

.form__item_type_error {
  border-bottom: 1px solid rgba(255, 0, 0, 1);
}

.form__item-error {
  visibility: hidden;
  min-height: 30px;
  color: #ff0000;
  font-size: 12px;
  line-height: 15px;
  box-sizing: border-box;
  display: block;
}

.form__item-error_active {
  visibility: visible;
}

.form__save-button {
  background-color: #000000;
  border-radius: 2px;
  width: 100%;
  height: 50px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  line-height: calc(18 / 21.8);
  margin: 17px 0 0;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;
}

.form__save-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 583px) {
  .form__save-button {
    font-size: 14px;
    font-weight: 400;
    line-height: calc(14 / 17);
    margin: 48px 0 0;
  }
}

.form__save-button_disabled {
  border: 1px solid #000000;
  background: #e5e5e5;
  color: #000000;
  opacity: 0.2;
  cursor: default;
}

.form__save-button_disabled:hover {
  opacity: 0.2;
}

.form__close-button {
  position: absolute;
  width: 32px;
  height: 32px;
  top: -40px;
  right: -40px;
  background-image: url(/static/media/close-Icon.e2e95a17.svg);
  background-color: rgba(0, 0, 0, 0);
  background-size: cover;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;
}

.form__close-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 583px) {
  .form__close-button {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}



